        <p>
          –ü—Ä–∏ <b>–ø–æ–∫—É–ø–∫–µ</b> —Ç–æ–≤–∞—Ä–æ–≤ —Å–æ –∑–Ω–∞—á–∫–æ–º –∏–∑—É–º—Ä—É–¥–∞ <span><img class="bonus-chip" src="/img/crystal.png" width="14px"/></span>, –≤—ã –ø–æ–ª—É—á–∞–µ—Ç–µ –Ω–∞ —Å–≤–æ–π —Å—á—ë—Ç —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–æ–Ω—É—Å–æ–≤.
        </p>
        <p>
          –ë–æ–Ω—É—Å—ã –¥–µ–π—Å—Ç–≤—É—é—Ç 6 –º–µ—Å—è—Ü–µ–≤.
        </p>
      </div>
    </ion-accordion>
    <ion-accordion value="second">
      <ion-item slot="header" color="light">
        <ion-label>–ö–∞–∫ <b>–ø–æ—Ç—Ä–∞—Ç–∏—Ç—å</b> –±–æ–Ω—É—Å—ã?</ion-label>
      </ion-item>
      <div class="ion-padding" slot="content" style="color:#666">
        <p>
          –í—ã –º–æ–∂–µ—Ç–µ <b>–ø–æ—Ç—Ä–∞—Ç–∏—Ç—å</b> –±–æ–Ω—É—Å—ã –Ω–∞ —Ä–µ–∞–ª—å–Ω—ã–µ –ø–æ–∫—É–ø–∫–∏. –ü—Ä–∏ —ç—Ç–æ–º <b>1 –∏–∑—É–º—Ä—É–¥ = 1 —Ä—É–±–ª—å</b>. 
        </p>
        <p>
          –¢–∞–∫ –≤—ã –º–æ–∂–µ—Ç–µ –æ–ø–ª–∞—Ç–∏—Ç—å –¥–æ <b>100%</b> —Å–ª–µ–¥—É—é—â–µ–≥–æ –∑–∞–∫–∞–∑–∞! –¢–æ–≤–∞—Ä—ã, —Å–æ –∑–Ω–∞—á–∫–æ–º –∏–∑—É–º—Ä—É–¥–∞ <span><img class="bonus-chip" src="/img/crystal.png" width="14px"/></span>, –º–æ–∂–Ω–æ –∫—É–ø–∏—Ç—å –ø–æ —Ü–µ–Ω–µ –¥–æ 1 —Ä—É–±–ª—è.
        </p>
      </div>
    </ion-accordion>
  </ion-accordion-group>
  <div  v-if="dataset.bonus_gained || dataset.bonus_spent">
    <div class="ion-padding-horizontal"> 
      <h5>–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –±–æ–Ω—É—Å–æ–≤</h5>
      <p style="margin-top: 0">–ó–∞ –ø–æ—Å–ª–µ–¥–Ω–∏–µ 30 –¥–Ω–µ–π</p>
    </div>
    <ion-grid>
      <ion-row>
        <ion-col size="12" size-sm="4"><statistics-card v-if="dataset.bonus_gained" label="–ó–∞—Ä–∞–±–æ—Ç–∞–Ω–æ" :data="dataset.bonus_gained" format="integer" color="#008FFB" :dates="statisticsDates"/></ion-col>
        <ion-col size="12" size-sm="4"><statistics-card v-if="dataset.bonus_spent" label="–ü–æ—Ç—Ä–∞—á–µ–Ω–æ" :data="dataset.bonus_spent" format="integer" color="#04e398" :dates="statisticsDates"/></ion-col>
      </ion-row>
    </ion-grid>
  </div>
</template>

<script>
import {
  IonAccordionGroup,
  IonAccordion,
  IonLabel,
  IonChip,
  IonItem,
  IonGrid,
  IonRow,
  IonCol,
  IonText,
}                   from "@ionic/vue";
import {  
  timeOutline, 
  searchOutline
 }                  from 'ionicons/icons'
 
import jQuery           from 'jquery'
import StatisticsCard          from "@/components/StatisticsCard.vue";

export default {
  components: {
    IonAccordionGroup,
    IonAccordion,
    IonLabel,
    IonChip,
    IonItem,
    IonGrid,
    IonRow,
    IonCol,
    IonText,
    StatisticsCard
  },
  setup(){
      return {
        timeOutline,
        searchOutline
      }
  },
  data() {
    return {
      dataset: {},
      bonusTotal: 0,
      statisticsDates: []
    };
  },
  computed: {
  },
  methods: {
    async listGet(){
        try{
            const request = {
                user_id: this.$heap.state.user.user_id,
                point_span: 3,
                point_num: 10
            }
            const response = await jQuery.post(`${this.$heap.state.hostname}Statistics/bonusParametersGet`, request)
            response.body.reverse()
            this.bonusTotal = response.head.bonus_total??0
            this.statisticsDates = this.parseDates(response)
            this.parseStatisticResponse(response)
            console.log(this.dataset)
        }catch{/** */}
    },
    parseStatisticResponse(response){
        const bars = {
          bonus_gained: true,
          bonus_spent: true
        }
        for(var param in bars){
          let dataset={
            [param]:{
              value: 0,
              series: []
            }
          }
          for(var i in response.body){
            if(response.body[i][param]){
              dataset[param].series.push(response.body[i][param])
              dataset[param].value += response.body[i][param]*1
            }
          }
          if(dataset[param].series.length){
            this.dataset=dataset
          }
        }
    },
    parseDates(response){
        let result = [];
        for(var i in response.body){
            result.unshift(response.body[i].point_finish)
        }
        return result
    }
  },
  mounted(){
    this.listGet();
  },
  activated(){
    this.listGet();
  }
};
</script>

